$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'navigation-item';

.#{$className} {
  text-decoration: none;
  color: $dark-shade-50;
  position: relative;

  &__header-wrapper {
    align-items: center;
    display: flex;
    cursor: pointer;
  }

  &__content {
    line-height: toRem(17);
    font-size: toRem(14);
  }

  &__expand-button {
    width: toRem(24);
		height: toRem(24);
    cursor: pointer;

    &::before {
      color: $dark-shade-50;
      font-size: toRem(24);
      font-family: 'Material Icons', Arial, sans-serif;
      content: 'expand_more';
	  }
  }

  &__sub-navigation {
    display: none;
    list-style-type: none;
    background-color: white;
    width: 100%;

    @media (min-width: $xl) {
      transform: translateY(toRem(24));
      min-width: toRem(250);
      position: absolute;
      top: 0;
      z-index: 2;
      border: toRem(1) solid rgba(0, 0, 0, 0.12);
      border-radius: toRem(2);
      visibility: hidden;
      opacity: 0.0;
      display: initial;
    }
  }

  &__sub-navigation-item {
    padding: toRem(10) 0 0 toRem(10);

    @media (min-width: $xl) {
      border-bottom: toRem(1) solid rgba(0, 0, 0, 0.12);
      line-height: toRem(17);
      padding: toRem(5);
  
      &:last-child {
        border-bottom: initial;
      }
    }
  }

  &__sub-navigation-item-content {
    text-decoration: none;
    color: $dark-shade-50;
  }

  &:hover,
  &__sub-navigation:hover {
    .#{$className} {
      &__sub-navigation {
        visibility: visible;
        opacity: 1.0;
      }
    }
  }

  &_opened {
    .#{$className}__sub-navigation {
      display: initial;
    }
  }

  &_active {
    font-weight: 700;
    color: $dark-shade-75;
  }
}