$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'header';

.#{$className} {
  background-color: white;
  box-shadow: toRem(0) toRem(10) toRem(20) $dark-shade-5;

  &_opened {
    .#{$className} {
      &__content,
      &__sign-in {
        display: flex;
        flex-direction: column;
        gap: toRem(20);
      }
    }
  }

  &__body {
    min-height: toRem(70);
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 2;
    position: relative;
    padding: 0 toRem(20);

    @media (min-width: $xl) {
      padding: 0 toRem(140);
    }
  }

  &__menu-button {
    font-size: toRem(24);
    font-family: 'Material Icons', Arial, sans-serif;
    color: $dark-shade-50;
    cursor: pointer;

    &::before {
      content: 'menu';
    }

    @media (min-width: $xl) {
      display: none;
    }
  }

  &__separator {
    width: toRem(1);
    height: toRem(30);
    background-color: $dark-shade-10;
  }

  &__user-name {
    color: $dark-shade-50;
  }

  &__content,
  &__sign-in {
    display: none;

    @media (min-width: $xl) {
      align-items: center;
      gap: toRem(20);
      display: flex;
    }
  }

  &__content {
    background-color: white;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: toRem(20);
    box-shadow: toRem(0) toRem(20) toRem(20) $dark-shade-5;
    border-top: toRem(1) solid $dark-shade-10;

    @media (min-width: $xl) {
      position: initial;
      border: initial;
      box-shadow: initial;
      width: initial;
      padding: initial;
    }
  }
}